<template>
  <div class="admin_dialog">
    <el-dialog :title="dialogParam.title"  :visible.sync="dialogParam.visible"  width="30%"  center>
      <div class="admin_dialog_body"><h4>您确认需要退出吗</h4></div>
      <div class="admin_dialog_footer">
        <el-button @click="dialogParam.visible = false">取 消</el-button>
        <el-button type="primary" @click="exit()">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { mapState, mapActions } from 'Vuex'
export default {
  name: 'exit',
  data () {
    return {}
  },
  props: {
    dialogParam: {
      type: Object,
      default () {
        return {}
      }
    }
  },
  beforeMount () {
  },
  watch: {
  },
  methods: {
    exit () {
      this.$router.push({
        path: '/'
      })
      this.dialogParam.visible = false
    },
    ...mapActions({
      userLogin: 'userLogin'
    })
  },
  computed: {
    ...mapState({
      basePath: 'basePath',
      userName: 'userName'
    })
  }

}
</script>
<style lang="less">
  .admin_dialog {
    .el-dialog__header {
      padding: 10px;
    }
    .el-dialog__headerbtn {
      top:10px;
      right: 10px;
    }
    .el-dialog__body {
      text-align: center;
      padding-bottom: 0;
      padding-top: 0;
      .admin_dialog_body {
        line-height: 0;
        text-align: center;
        padding: 40px 20px 20px;
        h4 {
          font-size: 14px;
        }
        .admin_icon {
          li {
            margin: 0 10px;
          }
        }
      }
      .admin_dialog_footer {
        margin: 10px auto;
        padding: 25px 20px;
        button {
          span {
            font-size: 16px;
          }
        }
        .el-button {
          padding: 8px 20px;
        }
      }
    }

  }
</style>



