<template>
  <div id="home">
    <el-container>
      <el-header><Top></Top></el-header>
      <el-container>
        <el-aside width="200px"><Leftmeun></Leftmeun></el-aside>
        <el-main><router-view/></el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import { mapState } from 'Vuex'
import Leftmeun from './components/leftmeun/index.vue'
import Top from './components/head/index.vue'
export default {
  name: 'Home',
  data () {
    return {}
  },
  components: {
    Leftmeun,
    Top
  },
  methods: {
  },
  computed: {
    ...mapState({
      basePath: 'basePath',
      userName: 'userName'
    })
  }

}
</script>

<style lang="less">
  @bg_height: 100%;
  #home{
    width: 100%;
    height: @bg_height;
    .el-container{
      height: @bg_height;
      .el-header {
        border-bottom:1px solid #d7d5d5;
      }
      .el-aside {
        background: #fff;
      }
      .el-main{
        font-size: 20px;
        background: #f4f4f4;
        padding: 10px;
      }
      .el-header {
        padding: 0;
      }
    }
  }
</style>



