<template>
  <div class="top" >
    <div id="head" class="flex flex_pack_justify">
      <div>
        <img alt="Vue logo" src="../../../../assets/img/content/index_01.gif">
      </div>
      <ul class="list flex flex_pack_justify">
        <li class="i1"></li>
        <li class="i2"></li>
        <li class="i3"></li>
        <li class="i4"></li>
        <li class="i5"></li>
        <li class="i6"></li>
        <li class="i7"  @click="drawer = true"><i class="el-icon-arrow-down"></i></li>
      </ul>
    </div>
    <Exit :dialog-param="dialogParam"></Exit>
    <el-drawer
      title="个人中心"
      :visible.sync="drawer"
      :direction="direction"
      :before-close="handleClose" size="350px">
      <div class="drawer">
         <div class="drawer_admin">
           <p class="drawer_img"></p>
           <p class="drawer_phone">13805883465</p>
           <p class="drawer_com">ceo@shningmi.com</p>
           <p><el-button>个人中心</el-button><el-button>安全退出</el-button></p>
         </div>
      </div>
      <div class="slider__info">
        <p class="slider__info-title">系统公告</p>
        <ul>
          <li class="text-hover">
            系统服务器升级公告 <span class="pull-right">2021-02-25</span>
          </li>
        </ul>
      </div>
    </el-drawer>
  </div>
</template>

<script>
import Exit from './exit.vue'
export default {
  name: 'Head',
  data () {
    return {
      drawer: false,
      dialogParam: {
        title: '',
        visible: false
      }
    }
  },
  components: {
    Exit
  },
  methods: {
    exit () {
      this.dialogParam = {
        title: '提示',
        visible: true
      }
    },
    handleClose(done) {
      this.drawer = false
    }
  },
  computed: {
  }

}
</script>

<style lang="less">
  #head{

    .list {
        padding-right: 10px;
        li {
          font-size: 12px;
          color: black;
          width: 64px;
          height: 54px;
          background-position: center;
        }
        .i1 {
          background: url("../../../../assets/img/content/erji.png") no-repeat;
          background-position: center;
        }
        .i2 {
          background: url("../../../../assets/img/content/dianhua.png") no-repeat;
          background-position: center;
        }
        .i3 {
          background: url("../../../../assets/img/content/plus-line.png") no-repeat;
          background-position: center;
        }
      .i4 {
        background: url("../../../../assets/img/content/shizhong.png") no-repeat;
        background-position: center;
      }
      .i5 {
        background: url("../../../../assets/img/content/naoling.png") no-repeat;
        background-position: center;
      }
      .i6 {
        background: url("../../../../assets/img/content/bangzhu.png") no-repeat;
        background-position: center;
       }
      .i7 {
        background: url("../../../../assets/img/content/noAvatar.a82e1f0.png") no-repeat;
        background-position: center;
        background-size: 100%;
        width: 30px;
        position: relative;
        margin-right: 20px;
        i {
          position: absolute;
          top: 22px;
          right: -20px;
        }
      }
      }
  }
  .v-modal {
    top:60px;
  }
  .el-drawer__wrapper {
    top:60px;
  }
  .el-drawer {
    .el-drawer__header {
      color: #222;
      padding: 0 15px;
      height: 40px;
      line-height: 40px;
      border-bottom: 1px solid #eaedef;
      span {
        font-size: 14px;
      }
    }
    .drawer {
      padding-bottom: 15px;
      .drawer_admin {
        text-align: center;
        p {

        }
        .drawer_img {
          background: url("../../../../assets/img/content/noAvatar.a82e1f0.png") no-repeat;
          width: 40px;
          height: 40px;
          background-position: center;
          background-size: 100%;
          display: inline-block;
        }
        .drawer_phone {
          font-size: 14px;
          font-weight: 700;
          margin-top: 5px;
        }
        .drawer_phone:hover{
          color: #e6001f!important;
          cursor: pointer;
        }
        .drawer_com {
          font-size: 12px;
          color: #222;
          line-height: 18px;
          margin-bottom: 12px;
        }
      }
    }
    .slider__info {
      line-height: 25px;
      padding: 10px 20px 8px 20px;
      border-top: 1px solid #eaedef;
      color: #606266;
      font-size: 12px;
      .slider__info-title {
        color: #d0021b;
        font-size: 14px;
      }
      .text-hover {
        line-height: 25px;
        padding: 10px 0 8px;
        color: #606266;
        font-size: 12px;
        .pull-right {
          float: right!important;
          line-height: 25px;
          padding: 10px 0 8px;
          color: #606266;
          font-size: 12px;
        }
      }
    }
  }
  .el-button {
    margin: 0 5px;
    padding: 8px 15px;
    span {
      font-size: 14px;
    }
  }
  .el-button:hover, .el-button:focus {
    color: #d0021b;
    border-color: #F3C0C6;
    background: #FCF2F3;
  }
</style>



